import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ModalController } from '@ionic/angular';
import { RoutesService } from 'src/app/services/routes.service';
import { UtilService } from 'src/app/services/util.service';
import { NotesDetailPage } from './notes-detail/notes-detail.page';
import { environment } from 'src/environments/environment';
import { ISoapMethodResponse, NgxSoapService, security } from 'ngx-soap';

const callcontent = `
      <codeLang xsi:type="xsd:string">${environment.soapLanguage}</codeLang>
      <poolAlias xsi:type="xsd:string">${environment.soapPoolAlias}</poolAlias>
      <poolId xsi:type="xsd:string">?</poolId>
      <requestConfig xsi:type="xsd:string">?</requestConfig>
`;
@Component({
  selector: 'app-document-detail',
  templateUrl: './document-detail.page.html',
  styleUrls: ['./document-detail.page.scss'],
})
export class DocumentDetailPage implements OnInit {
  detailDetId: string;
  documentDetails: any = [];
  constructor(
    private routesService: RoutesService,
    private route: ActivatedRoute,
    private utilService: UtilService,
    private modalController: ModalController,
    private soap: NgxSoapService
  ) {
    this.route.queryParams.subscribe(params => {
      if (params && params.document) {
        this.detailDetId = params.document;
        this.getDocumentDetails()
      }
    })
  }

  ngOnInit() {
  }

  async getDocumentDetails() {
    await this.utilService.showLoading();
    this.routesService.getDocumentDetails(this.detailDetId).subscribe((resp: any) => {
      this.documentDetails = resp;
      this.notesEditor(this.documentDetails[0].TEXTE1_0, 1);
      this.notesEditor(this.documentDetails[0].TEXTE2_0, 2);
      this.utilService.dismissLoading();
    }, (err: any) => {
      this.utilService.dismissLoading();
      this.utilService.showErrorCall(err);
    })
  }

  notesEditor(text, type) {
    let value = `
         <![CDATA[<PARAM>
      <FLD NAME="I_XNOTE" TYPE="Char">${text}</FLD>
      </PARAM>]]>`;
    this.soap.createClient(environment.soap + '/soap-wsdl/syracuse/collaboration/syracuse/CAdxWebServiceXmlCC?wsdl', {
      attributesKey: 'attributes', valueKey: '$value', xmlKey: '$xml'
    }).then(client => {
      client.setSecurity(new security.BasicAuthSecurity(environment.soapUsername, environment.soapPassword, ''))
      client.call('run', {
        callContext: {
          $xml: callcontent,
          attributes: {
            'xsi:type': "wss:CAdxCallContext"
          },
        },
        publicName: {
          attributes: {
            'xsi:type': "xsd:string"
          },
          $value: 'XRICHTEXTC'
        },
        inputXml: {
          attributes: {
            'xsi:type': "xsd:string"
          },
          $xml: value
        }
      }).subscribe((res: ISoapMethodResponse) => {
        this.utilService.dismissLoading();
        if (res.result.runReturn.resultXml?.$value) {
          const result = res.result.runReturn.resultXml.$value.RESULT.GRP.FLD;
          if (result[1].$value) {
            if (type == 1) {
              this.documentDetails[0].text1 = result[1].$value;
            } else {
              this.documentDetails[0].text2 = result[1].$value;
            }
          } else {
            if (text && text.trim() !== "") {
              this.utilService.showToast(result[1].$value);
            }
          }
        } else {
            this.utilService.showToast(res.result.runReturn.messages[0].message);
        }
      }, err => {
        console.log(err);
        this.utilService.showToast();
        this.utilService.dismissLoading();
      });
    }).catch(err => {
      console.log(err);
      this.utilService.showToast();
      this.utilService.dismissLoading();
    });
  }

  async notesModal(item) {
    const modal = await this.modalController.create({
      component: NotesDetailPage,
      componentProps: { docData: item }
    });

    await modal.present();

    const data = await modal.onDidDismiss();
    if (data && data.data) {

    }
  }
}
